/*global kakao*/

import React from "react";
import styled from "styled-components";
import CheckboxToggle from "../semi-component/toggle";
import Button from "../Button/Button";
import { X, Map } from "../Icons";
import Input from "../Input";
import "font-awesome/css/font-awesome.css";

import "flatpickr/dist/flatpickr.min.css";
import locale from "flatpickr/dist/l10n/ko";
import { BREAK_POINT_MOBILE } from "../../utils/mediaQuery";
import "../../Styles/css/mapCss.css";
import { toast } from "react-toastify";
import DatetimePicker, {
  parseDate,
  setLocale
} from "react-datetimepicker-syaku";
import DateInput from "../DateInput";
import DateLimitInput from "../DateLimitInput";
setLocale(locale.ko);

const AllContainer = styled.div`
  width: 700px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    font-size: 10pt;
  }
`;

const Container = styled.div`
  ${props => props.theme.whiteBox}
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;
const XButtonContainer = styled.div`
  display: flex;
  width: 100%;
  text-align: right;
  margin-bottom: 20px;
`;

const XButton = styled.button`
  width: 40px;
  border: 0;
  background-color: white;
  cursor: pointer;
`;

const ContentContainer = styled.div`
  width: 100%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  &:(:last-child) {
    margin-bottom: 10px;
  }
`;
const Content = styled(Input)`
  width: 100%;
`;
const ContentPlace = styled(Input)`
  width: 93%;
  ${props => {
    if (props.meetingId !== "write") {
      return " width:100%";
    }
  }}
`;

const MapButton = styled.span`
  width: 5%;
  font-size: 9pt;
  margin-left: 10px;
  opacity: 0.9;
  cursor: pointer;
  display: flex;
  align-items: center;
`;

const MapContainer = styled.div`
  display: flex;
  justify-content: center;
  width:100%:
  padding-bottom:20px;
`;

const SpinnerContainer = styled.div`
  padding: 10px;
  width: 600px;
  display: flex;
  flex-direction: row;
  font-size: 11pt;
  align-items: center;
  &: {
    margin-left: 10px;
  }
`;
const CheckboxToggleM = styled(CheckboxToggle)`
  margin-left: 10px;
`;
const SubmitButton = styled(Button)`
  margin-top: 15px;
`;
const ButtonContainer = styled.div`
  margin-top: 10px;
  width: 100%;
`;

export default ({
  meetingId,
  title,
  meetingTime,
  meetingPlace,
  meetingPrice,
  deadline,
  meetingHeadCounts,
  setEdit,
  mainCheck,
  clickCheck,
  onSubmit,
  deleteMeeting,
  mapAction,
  mapClick,
  setMarker,
  setState,
  setLimitState,
  coordsParam,
  translate,
  dateTime,
  loadingB
}) => {
  console.log(deadline);
  if (mapAction === true) {
    if (meetingId === "write") {
      const script = document.createElement("script");
      script.async = true;
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?appkey=f2fb500392c56034d629914c8b7465c7";
      document.head.appendChild(script);

      script.onload = () => {
        kakao.maps.load(() => {
          let el = document.getElementById("map");
          let map = new kakao.maps.Map(el, {
            center: new kakao.maps.Coords(523951.25, 1085073.75),
            draggable: true,
            scrollWheel: true
          });

          let infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
          let ps = new kakao.maps.services.Places();

          //   
          ps.keywordSearch(meetingPlace.value, placesSearchCB);
          //       
          function placesSearchCB(data, status, pagination) {
            if (status === kakao.maps.services.Status.OK) {
              //       
              // LatLngBounds   
              var bounds = new kakao.maps.LatLngBounds();

              for (var i = 0; i < data.length; i++) {
                displayMarker(data[i]);
                bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
              }

              //       
              map.setBounds(bounds);
            }
          }

          //    
          function displayMarker(place) {
            //    
            var marker = new kakao.maps.Marker({
              map: map,
              position: new kakao.maps.LatLng(place.y, place.x)
            });

            //   
            kakao.maps.event.addListener(marker, "click", function() {
              //     

              infowindow.setContent(
                '<div style="padding:5px;font-size:12px;">' +
                  place.place_name +
                  "</div>"
              );
              infowindow.open(map, marker);
              setMarker.current = marker.getPosition();
              if (setMarker.current !== "") {
                toast.success(" .");
                mapClick(false);
              }
            });
          }
        });
      };
    } else {
      const script = document.createElement("script");
      script.async = true;
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?appkey=f2fb500392c56034d629914c8b7465c7";
      document.head.appendChild(script);
      var coords = translate(coordsParam);

      script.onload = () => {
        kakao.maps.load(() => {
          let el = document.getElementById("map");
          let map = new kakao.maps.Map(el, {
            center: new kakao.maps.LatLng(
              Number(coords.split(",")[0]),
              Number(coords.split(",")[1])
            ),
            draggable: true,
            scrollWheel: true
          });
          let infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
          var marker = new kakao.maps.Marker({
            map: map,
            position: new kakao.maps.LatLng(
              Number(coords.split(",")[0]),
              Number(coords.split(",")[1])
            )
          });

          //   
          kakao.maps.event.addListener(marker, "click", function() {
            //     
            infowindow.setContent(
              '<div style="padding:5px;font-size:12px;">' +
                meetingPlace.value +
                "</div>"
            );
            infowindow.open(map, marker);
            setMarker.current = marker.getPosition();
            if (setMarker.current !== "") {
              toast.success(" .");
              mapClick(false);
            }
          });
        });
      };
    }
  }

  if (meetingId === "write") {
    return (
      <AllContainer>
        <XButtonContainer>
          <XButton onClick={() => setEdit("read")}>
            <X />
          </XButton>
        </XButtonContainer>

        <Container className="container">
          <form onSubmit={onSubmit}>
            <ContentContainer>
              <Content placeholder={""} {...title} />
            </ContentContainer>
            <SpinnerContainer>
              <CheckboxToggleM
                checked={mainCheck}
                onClick={() => clickCheck()}
              />{" "}
              
            </SpinnerContainer>
            <ContentContainer>
              <DateInput setState={() => setState()} value={meetingTime} />{" "}
            </ContentContainer>

            <ContentContainer>
              <Content placeholder={" "} {...meetingPrice} />
            </ContentContainer>
            <ContentContainer>
              <DateLimitInput
                setState={() => setLimitState()}
                value={deadline}
              />{" "}
            </ContentContainer>
            <ContentContainer>
              <Content
                type="number"
                placeholder={" "}
                {...meetingHeadCounts}
              />
            </ContentContainer>
            <ContentContainer>
              <ContentPlace placeholder={" "} {...meetingPlace} />
              <MapButton onClick={() => mapClick()}>
                <Map />
              </MapButton>
            </ContentContainer>
            <MapContainer>
              {mapAction ? <div id="map"></div> : null}
            </MapContainer>
            <SubmitButton text={" "} loading={loadingB} />
          </form>
        </Container>
      </AllContainer>
    );
  } else {
    return (
      <AllContainer>
        <XButtonContainer>
          <XButton onClick={() => setEdit("read")}>
            <X />
          </XButton>
        </XButtonContainer>
        <Container>
          <form onSubmit={onSubmit}>
            <ContentContainer>
              <Content placeholder={""} {...title} />
            </ContentContainer>
            <SpinnerContainer>
              <CheckboxToggleM
                checked={mainCheck}
                onClick={() => clickCheck()}
              />{" "}
              
            </SpinnerContainer>
            <ContentContainer>
              <DateInput setState={setState} value={meetingTime} />{" "}
            </ContentContainer>
            <ContentContainer>
              <Content placeholder={" "} {...meetingPrice} />
            </ContentContainer>
            <ContentContainer>
              <DateLimitInput setState={setLimitState} value={deadline} />{" "}
            </ContentContainer>
            <ContentContainer>
              <Content
                placeholder={" "}
                type="number"
                {...meetingHeadCounts}
              />
            </ContentContainer>

            <ContentContainer>
              <ContentPlace
                placeholder={" "}
                {...meetingPlace}
                disabled
              />
              {/* <MapButton onClick={() => mapClick()}>
                <Map />
              </MapButton> */}
            </ContentContainer>
            {/* <MapContainer>
              {mapAction ? <div id="map"></div> : null}
            </MapContainer> */}
            <ButtonContainer>
              <SubmitButton text={""} loading={loadingB} />
            </ButtonContainer>
          </form>
          <ButtonContainer>
            <Button text={" "} onClick={() => deleteMeeting()} />
          </ButtonContainer>
        </Container>
      </AllContainer>
    );
  }
};
