import React from "react";
import styled, { keyframes } from "styled-components";
import Avatar from "../../Components/Avatar";
import FatText from "../../Components/FatText";
import Button from "../../Components/Button/Button";
import { Helmet } from "react-helmet";
import Loader from "../../Components/Loader";
import ProfileBulletinLine from "../../Components/ProfileBulletinLine";
import CommentLine from "../../Components/CommentLine";
import MeetingLine from "../../Components/MeetingLine";
import EditProfile from "../../Components/EditProfile";
import { BREAK_POINT_MOBILE } from "../../utils/mediaQuery";
import ProfileUpdate from "../../Components/ProfileUpdate";
import Bulletin from "../../Components/Bulletin";
const Animation = keyframes`
    0%{
        opacity:1
    }
    3%{
        opacity:0.3
    }
    100%{
        opacity:0.3
    }
`;
const Wrapper = styled.div`
  min-height: 80vh;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  ${props => props.size}
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10pt;
  }
`;
const Header = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 100px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10pt;
  }
`;
const HeaderColumn = styled.div`
  display: flex;
  flex-direction: column;
  &:first-child {
    align-self: top;
  }
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
  }
`;
const AvatarM = styled(Avatar)`
  &:hover {
    opacity: 0.3;
  }
  cursor: pointer;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    ${props => (props.size = "md")}
    margin-bottom: 10px;
  }
`;
const UsernameRow = styled.div`
  display: flex;
  height: 100px;
  justify-content: center;
  align-itmes: center;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    display: flex;
    flex-direction: column;
    height: auto;
  }
`;
const UserName = styled(FatText)`
  font-size: 20pt;
  text-align: center;
  width: 300px;
  padding: 30px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    padding: 10px;
  }
`;
const WholeButtonContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

const ButtonContainer = styled.div`
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 10px;

  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
    padding: 5px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
`;
const ProfileButton = styled(Button)`
  width: 100%;

  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
  }
`;
const ProfileContainer = styled.div`
  width:100%:
  height:200px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding:40px;
  padding-left:110px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width:100%;
    margin-top:10px;
    padding:10px;
  }
`;
const ProfileTextContainer = styled.div`
  margin-bottom: 20px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
    margin-bottom: 5px;
    text-align: center;
  }
`;

const ProfileInfo = styled(FatText)`
  height: 30px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    font-size: 11pt;
  }
`;

const Counts = styled.ul`
  display: flex;
  margin: 15px 0px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    margin: 5px;
    width: 100%;
    text-align: center;
  }
`;

const Count = styled.li`
  font-size: 16px;
  margin: 20px;
  font-weight: 700;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    font-size: 10pt;
    margin: 20px;
  }
`;

const MainContainer = styled.div`
  width: 800px;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
  }
`;

const TabContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  height: 60px;
  align-items: center;
  margin-bottom: 10px;
  border-top: 0.5px solid ${props => props.theme.lightGray3};
  border-bottom: 0.5px solid ${props => props.theme.lightGray3};
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 100%;
    height: 40px;
  }
`;
const Tab = styled.span`
  &:hover {
    animation: ${Animation} 10s linear infinite;
  }
  cursor: pointer;
  width: 250px;
  text-align: center;
  font-weight: 700;
  @media (max-width: ${BREAK_POINT_MOBILE}px) {
    width: 33%;
  }
`;

const ContentContainer = styled.div`
  width: 80%;
  height: 600px;
  display: flex;
  flex-direction: column;

  margin: 0 auto;
`;

export default ({
  loading,
  data,
  logOut,
  action,
  setAction,
  editAction,
  editProfile,
  update,
  setUpdate,
  refetch
}) => {
  if (loading) {
    return (
      <Wrapper>
        <Loader />
      </Wrapper>
    );
  } else if (!loading && data && data.seeProfile) {
    const {
      seeProfile: {
        id,
        avatar,
        userName,
        birthday,
        phoneNum,
        email,
        classes,
        posts,
        postsCount,
        comments,
        commentsCount,
        reservations,
        reservationsCount,
        likes,
        likesCount,
        participants,
        participantsCount,
        isSelf,
        createdAt
      }
    } = data;
    if (editAction === "Profile") {
      return (
        <Wrapper>
          <Helmet>
            <title>{userName} | Originals</title>
          </Helmet>
          {update === false ? (
            <Header>
              <HeaderColumn>
                <AvatarM
                  size={"lg"}
                  url={avatar}
                  onClick={() => setUpdate(true)}
                />
              </HeaderColumn>
              <HeaderColumn>
                <UsernameRow>
                  <UserName text={userName} />

                  {isSelf ? (
                    <WholeButtonContainer>
                      <ButtonContainer>
                        <ProfileButton
                          onClick={() => editProfile("Edit")}
                          text={" "}
                        />
                      </ButtonContainer>

                      <ButtonContainer>
                        <ProfileButton onClick={logOut} text={""} />
                      </ButtonContainer>
                    </WholeButtonContainer>
                  ) : null}
                </UsernameRow>

                <ProfileContainer>
                  {isSelf ? (
                    <>
                      <ProfileTextContainer>
                        <ProfileInfo text={" : " + email} />
                      </ProfileTextContainer>
                      <ProfileTextContainer>
                        <ProfileInfo text={" : " + birthday} />
                      </ProfileTextContainer>
                      <ProfileTextContainer>
                        <ProfileInfo text={" : " + phoneNum} />
                      </ProfileTextContainer>
                    </>
                  ) : null}
                  <ProfileTextContainer>
                    {classes === 1 ? (
                      <ProfileInfo text={""} />
                    ) : classes === 2 ? (
                      <ProfileInfo text={""} />
                    ) : (
                      <ProfileInfo text={""} />
                    )}
                  </ProfileTextContainer>
                </ProfileContainer>

                {/* isSelf   ,  ,   */}
                {/*   */}
                <Counts>
                  <Count>
                    <FatText text={String(postsCount)} /> 
                  </Count>
                  <Count>
                    <FatText text={String(commentsCount)} /> 
                  </Count>
                  <Count>
                    <FatText text={String(participantsCount)} />  
                  </Count>
                  {/* <Count>
                <FatText text={String(postsCount)} />  
              </Count> */}
                </Counts>
              </HeaderColumn>
            </Header>
          ) : (
            <ProfileUpdate setUpdate={setUpdate} refetch={refetch} />
          )}
          <MainContainer>
            <TabContainer>
              <Tab onClick={() => setAction("bulletin")}></Tab>
              <Tab onClick={() => setAction("comment")}></Tab>
              <Tab onClick={() => setAction("meeting")}> </Tab>
            </TabContainer>
            <ContentContainer>
              {action === "bulletin" ? (
                posts.map(post => (
                  <ProfileBulletinLine
                    key={post.id}
                    post={post}
                    action={"main"}
                    setAction={setAction}
                  />
                ))
              ) : action === "comment" ? (
                comments.map(comment => (
                  <CommentLine key={comment.id} comment={comment} />
                ))
              ) : action === "meeting" ? (
                participants.map(participant => (
                  <MeetingLine key={participant.id} participant={participant} />
                ))
              ) : (
                <Bulletin postId={action} setAction={setAction} />
              )}
            </ContentContainer>
          </MainContainer>
        </Wrapper>
      );
    } else if (editAction === "Edit") {
      return <EditProfile id={id} editProfile={editProfile} logOut={logOut} />;
    }
  } else {
    return null;
  }
};
